import * as signalR from '@microsoft/signalr';

const backendURL = 'https://beknkpt2.dvtien.id.vn';

let connection = null; // Bin ton cc lu tr kt ni SignalR

// Hm khi to v kt ni SignalR
const startSignalRConnection = async (token) => {
    if (connection) {
        //console.log('SignalR connection already established.');
        return connection;
    }

    connection = new signalR.HubConnectionBuilder()
        .withUrl(`${backendURL}/notificationHub`, {
            accessTokenFactory: () => token,
            transport: signalR.HttpTransportType.WebSockets |
                        signalR.HttpTransportType.ServerSentEvents |
                        signalR.HttpTransportType.LongPolling
        })
        .withAutomaticReconnect([0, 2000, 10000, 30000])
        .configureLogging(signalR.LogLevel.Information)
        .build();

    // Khi to kt ni, tr v promise  x l sau
    try {
        await connection.start(); // Bt u kt ni
        //console.log("Connected to SignalR hub.");

        // ng k s kin nhn tin nhn, gi callback khi nhn c tin nhn
        connection.on("ReceiveMessage", (message) => {
            //console.log("Received message:", message);
            if (onReceiveMessage) {
                onReceiveMessage(message); // Gi callback c truyn vo
            }
        });

    } catch (error) {
        //console.error("Error connecting to SignalR hub:", error);
        setTimeout(() => startSignalRConnection(token, onReceiveMessage), 5000); // Th kt ni li sau 5 giy nu gp li
    }
};

// Hm ng k s kin ReceiveMessage
const onReceiveMessage = (callback) => {
    if (connection) {
        connection.on("ReceiveMessage", callback);
    } else {
        //console.error("Connection has not been established yet.");
    }
};

// Hm dng kt ni SignalR
const stopSignalRConnection = async () => {
    if (connection) {
        try {
            await connection.stop();
            //console.log("Disconnected from SignalR hub.");
            connection = null;
        } catch (error) {
            //console.error("Error disconnecting from SignalR hub:", error);
        }
    }
};

const removeFromGroup = async () => {
    if (connection) {
        try {
            await connection.invoke('RemoveFromGroup');
            //console.log("User  logout SignalR.");
        } catch (error) {
            //console.error(`Failed to remove user from group: ${error}`);
        }
    } else {
        //console.error("Connection has not been established yet.");
    }
};

// Hm kim tra nu  kt ni
const isConnected = () => {
    return connection !== null;
};

export {
    startSignalRConnection , stopSignalRConnection, onReceiveMessage, removeFromGroup, isConnected
}