import React, { useState, useContext, useEffect, useRef } from 'react';
import { useHistory } from 'react-router-dom';
import _ from "lodash";
//context
import { UserContext } from '../../context/UserContext';
//modal add user
import AddUser from '../ManageAddUser/AddUser';
//mui theme
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbarExport, GridToolbarColumnsButton, GridToolbarDensitySelector, GridToolbarFilterButton, GridToolbarContainer, viVN } from '@mui/x-data-grid';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
import Button from '@mui/material/Button';
import Switch from '@mui/material/Switch';
//icon
import { HiUserAdd } from "react-icons/hi";
//api
import { getListUser } from '../../Service/StatisticsService';
import { updateActiveUser } from '../../Service/UserService';
import { toast } from 'react-toastify';


function MainListUser() {
    const { user, loading } = useContext(UserContext);

    const [reloadComponent, setReloadComponent] = useState(false);
    const [loadingListUser, setLoadingListUser] = useState(false);
    const [listUser, setListUser] = useState([]);

    const [openModalAddUser, setOpenModalAddUser] = useState(false);

    const history = useHistory();

    const onChangeActiveUser = async (event, data) => {
        setLoadingListUser(true);
        const responseUpdateActiveUser = await updateActiveUser(data.userId, event.target.checked);
        if(responseUpdateActiveUser.status === 200){
            const responseGetListUser = await getListUser();
            const editMainDataListUser = responseGetListUser.map((item) => {
                const dataReportUser = {
                    userId: item.userId,
                    userName: item.userName,
                    userFullName: item.userFullName,
                    userEmail: item.userEmail,
                    positionName: item.positionName,
                    userIsActive: item.userIsActive
                }

                if(item.positionName === 'Doctor'){
                    dataReportUser.positionName = 'Bc s'
                }
                else if(item.positionName === 'Nursing'){
                    dataReportUser.positionName = 'Tip nhn'
                }

                return dataReportUser
            })
            setListUser(editMainDataListUser);
            
            toast.success(event.target.checked === true ? ` v hiu ha ngi dng ${data.userFullName}` : ` kch hot li ngi dng ${data.userFullName}`)
        }
        setLoadingListUser(false);
    }

    const columns = [
        { field: 'userName', headerName: 'Tn ng nhp', width: 240, align: 'left', headerAlign: 'left', },
        { field: 'userFullName', headerName: 'H tn', width: 240, align: 'left', headerAlign: 'left', },
        { field: 'userEmail', headerName: 'Email', width: 340, align: 'left', headerAlign: 'left', },
        { field: 'positionName', headerName: 'V tr', width: 200, align: 'left', headerAlign: 'left' },
        { field: 'userIsActive', headerName: 'Trng thi', width: 165, align: 'left', headerAlign: 'left', renderCell: (params) => params.row.userIsActive ? <Switch checked={true} onChange={(e) => onChangeActiveUser(e, params.row)} /> : <Switch checked={false} onChange={(e) => onChangeActiveUser(e, params.row)}/> },
    ];

    // Custom toolbar bao gm nt thm user
    const CustomToolbar = ({ handleOpenModalAddUser }) => {
        return (
            <GridToolbarContainer >
                {/* Cc chc nng ca GridToolbar mc nh */}
                <GridToolbarColumnsButton sx={{fontSize: '16px'}}/>
                <GridToolbarFilterButton sx={{fontSize: '16px'}}/>
                <GridToolbarDensitySelector sx={{fontSize: '16px'}}/>
                <Button variant="text" startIcon={<HiUserAdd/>} onClick={handleOpenModalAddUser} sx={{fontSize: '16px'}}>Thm ngi dng</Button>
            </GridToolbarContainer>
        );
    };

    const handleOpenModalAddUser = () => {
        setOpenModalAddUser(true);
    }

    const handleGetListUser = async () => {
        setLoadingListUser(true);
        const responseGetListUser = await getListUser();
        const editMainDataListUser = responseGetListUser.map((item) => {
            const dataReportUser = {
                userId: item.userId,
                userName: item.userName,
                userFullName: item.userFullName,
                userEmail: item.userEmail,
                positionName: item.positionName,
                userIsActive: item.userIsActive
            }

            if(item.positionName === 'Doctor'){
                dataReportUser.positionName = 'Bc s'
            }
            else if(item.positionName === 'Nursing'){
                dataReportUser.positionName = 'Tip nhn'
            }

            return dataReportUser
        })
        setListUser(editMainDataListUser);
        setLoadingListUser(false);

        if(reloadComponent){
            setReloadComponent(false);
        }
    }

    useEffect(() => {
        handleGetListUser();
    }, [])

    useEffect(() => {
        if(reloadComponent){
            handleGetListUser();
        }
    }, [reloadComponent])

    useEffect(() => {
        if(loading === false && user.isLogin){
            if(user.positionName !== 'Admin'){
                history.push('/404');
            }
        }
    }, [loading, user])

    return (
        <>
            <Container maxWidth="xl" sx={{mt: 1}}>
                <Box sx={{ height: 600, width: '100%', mt: 1.2 }}>
                    {loadingListUser ? 
                        <>
                            <Box sx={{display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column', height: '100%'}}>
                                <CircularProgress />
                                <Typography variant='subtitle1' sx={{mt: 1}}>ang ti d liu, hy ch mt cht...</Typography>
                            </Box>  
                        </>
                    :
                        <>  
                            <Typography variant='h5' sx={{textAlign: 'center', mb: 1, fontWeight: 'bolder', color: 'red'}}>Danh sch ngi dng</Typography>     
                            <DataGrid 
                                style={{ fontSize: '18px', marginLeft: '50px', marginRight: '50px' }}
                                localeText={viVN.components.MuiDataGrid.defaultProps.localeText}
                                rows={listUser.map((row, index) => ({
                                    ...row,
                                    id: index + 1,
                                }))}
                                columns={columns}
                                pageSizeOptions={[1]}
                                components={{
                                    Toolbar: CustomToolbar, // Chn toolbar vo y
                                  }}
                                componentsProps={{
                                    toolbar: { handleOpenModalAddUser },
                                }}
                                disableRowSelectionOnClick
                                sx={{ 
                                    '.MuiTablePagination-selectLabel': { marginBottom: '3px', }, 
                                    '.MuiTablePagination-displayedRows': { marginBottom: '3px', },
                                }}
                            />
                        </>
                    }
                </Box>
            </Container>
            
            <AddUser openModalAddUser={openModalAddUser} setOpenModalAddUser={setOpenModalAddUser} setReloadComponent={setReloadComponent}/>
        </>
    )
}

export default MainListUser