import React, { useState, useContext, useEffect, useRef } from 'react';
//mui theme
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
//context
import { UserContext } from '../../context/UserContext';

function Home() {

    const { user, loading } = useContext(UserContext);

    return (
        <Container> 
            <Box sx={{mt: 1}}>
                {loading === false && user.isLogin ?
                    user.positionName === 'Nursing' ? 
                        <>
                            <Typography variant='subtitle1' sx={{ml: 2, fontSize: '1.35rem'}}>Nu bn thy dng ch ny hy nhn vo <span style={{fontWeight: 'bolder', color: 'blue'}}>"ng k khm"</span>  thanh menu c logo ca bnh vin  chuyn v trang ng k khm</Typography>
                        </>
                    :
                        user.positionName === 'Doctor' ? 
                            <>
                                <Typography variant='subtitle1' sx={{ml: 2, fontSize: '1.35rem'}}>{`Kim tra thanh menu (c logo ca bnh vin) bn gc phi c hin tn ngi dng`}</Typography> 
                                <Typography variant='subtitle1' sx={{ml: 2, fontSize: '1.35rem'}}>Nu ng tn bc s ang khm hy nhn vo <span style={{fontWeight: 'bolder', color: 'blue'}}>"Khm bnh"</span>  chuyn v trang khm bnh</Typography>
                                <Typography variant='subtitle1' sx={{ml: 2, fontSize: '1.35rem'}}>Nu sai tn bc s ang khm hy nhn vo tn ngi dng  s hin ra menu nh, nhn ng xut</Typography>       
                            </>
                        :
                            null
                :
                    <Typography variant='subtitle1' sx={{ml: 2, fontSize: '1.35rem'}}>Nhn vo <span style={{fontWeight: 'bolder', color: 'blue'}}>"ng nhp"</span> trn thanh menu (c logo ca bnh vin) bn gc phi</Typography> 
                }
            </Box>
        </Container>
    )
}

export default Home